











import { Component, Prop, Vue } from 'vue-property-decorator'

@Component
export default class Button extends Vue {
  @Prop() private primary!: boolean;
  @Prop() private loading!: boolean;

  get btnClass () {
    let className = ''

    if (this.primary) {
      className += 'btn-primary'
    }

    return className
  }
}
