









import { Component, Prop, Vue } from 'vue-property-decorator'

@Component
export default class SkeletonBox extends Vue {
    maxWidth = 100;
    minWidth = 100;
    height = '1em';
    width = null;

    get computedWidth () {
      return this.width || `${Math.floor((Math.random() * (this.maxWidth - this.minWidth)) + this.minWidth)}%`
    }
}
